import styled from "styled-components";

export const Image = styled.img`

width:100%;
height: 800px;
max-height: 100%;
max-width: 100%;   
display:block;
background-repeat:no-repeat;
background-position: center;
margin: 0 auto;
background-size: contain;
background-image: url("/images/game-of-life/game-of-life.gif");
`

export const Photo = styled.div`
height:214px;
width:211px;
display:block;
margin-right:15px;
border-radius:107px;
background-position: center;
background-size: contain;
background-repeat:no-repeat;
float:left;
background-image: url("/images/myPhoto.png");
`
export const Header4Dark = styled.h4`
margin: 0 auto;
margin-left: 25px;
font-size:40px;
font-family: "Spartan", sans-serif; 
color: black; 
`
export const Header5Dark = styled.h5`
margin: 0 auto;
font-size:35px;
text-align:center;
font-family: "Raleway", sans-serif; 
color: black; 
`

export const HrDark = styled.hr`
color:black;
height:5px;
width:100%;
opacity:1;
`

export const Text = styled.p`
margin: 0 auto;
font-size:25px;
width:95%;
font-family:"Raleway", sans-serif;
`
export const Content = styled.div`
margin: 45px auto;
width: 90%;
padding: 15px;

`
export const A = styled.a`
text-decoration:none;
color: crimson;
`
export const Remark = styled.h5`
margin: 0 auto;
font-size:15px;
width:90%;
font-family:"Raleway", sans-serif;
font-style:underline;
`
export const Wrapper = styled.div`
border-left: 1px solid black;
margin: 0 auto 45px auto;

`